import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row,  Label} from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
import Loader from '../../Loader/Loader';
import './RegisteredCasesDetails.css';
class RegisteredCasesDetails extends Component {
  constructor(props){
    super(props);
    this.state = {
      loading: true,
      registeredCasesInfo: {threatType: [], howThreaten: [], modeOfThreat: [], dateOfIncident: [], incidentSupportDocs: [], companySupportDocs: [], instigatorProfile: [], victimProfile: [], othersProfile:[]}
    } 
    
    
    
  }
  
  
  componentDidMount() { 
    
    const { match: { params } } = this.props;
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('cases/'+params.registredCaseId)
        .then( res => {
          console.log(res);
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);  
            this.props.history.push('/user/registered-cases');  
            return;
          }   

          this.setState({loading:false, registeredCasesInfo: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
  }
  
  render() {

    const { registeredCasesInfo, loading} = this.state;     
    let loaderElement = '';
    if(loading)   
      loaderElement = <Loader />
    
    return (
      <div className="dashboard-section">
        {loaderElement}
        <Card className="vd-card">
          <CardHeader>
            <div className="d-flex align-items-center">
              <div className="mr-auto">
                <h4 className="card-title"><img src="/images/task.svg" height="30" alt="" /> Registered Case Details</h4>
              </div>
              
            </div>
          </CardHeader>
          <CardBody className="item-list-section">
            <div className="Search-filter">
              <Row>
                <Col md={6}><Label><strong>Case Id: </strong></Label><span> {registeredCasesInfo.caseAutoId || ''}</span></Col>
                <Col md={6}><Label><strong>Registred By: </strong></Label><span> {registeredCasesInfo.employeeName || 'N/A' }</span></Col>
                <Col md={4}><Label><strong>Case Type: </strong></Label><span> {registeredCasesInfo.caseTypeName || 'N/A'}</span></Col>
                <Col md={4}><Label><strong>Case Title: </strong></Label><span> {registeredCasesInfo.caseTitle || ''}</span></Col>
                <Col md={4}><Label><strong>Secondary to report threat: </strong></Label><span> {registeredCasesInfo.secondaryReportThreat || ''}</span></Col>
                <Col md={4}><Label><strong>Threat Type: </strong></Label><span> {registeredCasesInfo.threatType.join(',')}</span></Col>
                <Col md={12}><hr /></Col>
                <Col md={4}><Label><strong>How Threaten: </strong></Label><span> {registeredCasesInfo.howThreaten.join(',')}</span></Col>
                <Col md={4}><Label><strong>Mode of Threat: </strong></Label><span> {registeredCasesInfo.modeOfThreat.join(',')}</span></Col>
                <Col md={12}><Label><strong>Description of incident: </strong></Label><span> {registeredCasesInfo.descriptionOfIncident || ''}</span></Col>
                <Col md={6}><Label><strong>Date of incidents: </strong></Label><span> {registeredCasesInfo.dateOfIncident.join(',')}</span></Col>
                <Col md={12}><hr />
                  <h3>Incident Information</h3>
                </Col>
                <Col md={4}><Label><strong>Location: </strong></Label><span> {registeredCasesInfo.incidentLocation || 'N/A'}</span></Col>
                <Col md={4}><Label><strong>Name: </strong></Label><span> {registeredCasesInfo.incidentPersonName || 'N/A'}</span></Col>
                <Col md={4}><Label><strong>Number: </strong></Label><span> {registeredCasesInfo.incidentPersonNumber || 'N/A'}</span></Col>
                <Col md={4}><Label><strong>Email: </strong></Label><span> {registeredCasesInfo.incidentPersonEmail || 'N/A'}</span></Col>
                <Col md={12}><Label><strong>Department Informationa: </strong></Label><span> {registeredCasesInfo.incidentDepartmentDescription || 'N/A'}</span></Col>
                <Col md={12}><Label><strong>Supported Documnets:</strong></Label>
                  <div className="imagesPreview-section">
                    {
                      registeredCasesInfo.incidentSupportDocs.map((docs, index) => 
                         <div className="imagesPreview" key={index}>
                           <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                         </div>
                      )
                    }
                  </div>
                </Col>
                <Col md={12}><hr />
                  <h3>Involved Parties Instigator</h3>

                  
                </Col>
                <Col md={12}>
                  {
                      registeredCasesInfo.instigatorProfile.map((info, index) => 
                         <Row className="instigator-info" key={index}>
                             <Col md={4}><Label><strong>Name: </strong></Label><span> {info.instigatorName || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Contact: </strong></Label><span> {info.instigatorContact || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Date Of Birth: </strong></Label><span> {commonService.getDate(info.instigatorDOB)}</span></Col>
                             <Col md={4}><Label><strong>Address: </strong></Label><span> {info.instigatoraddress || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Social Information: </strong></Label><span> {info.instigatorSocialInformation || 'N/A'}</span></Col>
                         </Row>
                      )
                    }
                </Col>
                <Col md={12}><hr />
                  <h3>Involved Parties Victim</h3>

                </Col>
                <Col md={12}>

                  {
                      registeredCasesInfo.victimProfile.map((info, index) => 
                         <Row className="instigator-info" key={index}>
                             <Col md={4}><Label><strong>Name: </strong></Label><span> {info.victimName || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Contact: </strong></Label><span> {info.victimContact || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Date Of Birth: </strong></Label><span> {commonService.getDate(info.victimDOB)}</span></Col>
                             <Col md={4}><Label><strong>Address: </strong></Label><span> {info.victimaddress || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Social Information: </strong></Label><span> {info.victimSocialInformation || 'N/A'}</span></Col>
                         </Row>
                      )
                    }
                </Col>
                <Col md={12}><hr />
                  <h3>Involved Parties Others</h3>

                  
                </Col>
                <Col md={12}>
                  {
                      registeredCasesInfo.othersProfile.map((info, index) => 
                         <Row className="instigator-info" key={index}>
                             <Col md={4}><Label><strong>Name: </strong></Label><span> {info.othersName || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Contact: </strong></Label><span> {info.othersContact || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Date Of Birth: </strong></Label><span> {commonService.getDate(info.othersDOB)}</span></Col>
                             <Col md={4}><Label><strong>Address: </strong></Label><span> {info.othersaddress || 'N/A'}</span></Col>
                             <Col md={4}><Label><strong>Social Information: </strong></Label><span> {info.othersSocialInformation || 'N/A'}</span></Col>
                         </Row>
                      )
                    }
                </Col>
                <Col md={12}><hr />
                  <h3>Company Information</h3>
                </Col>
                <Col md={4}><Label><strong>Initial Threat Level: </strong></Label><span> {registeredCasesInfo.initialThreatLevel || '0'}</span></Col>
                <Col md={12}><Label><strong>Supported Documnets:</strong></Label>
                  <div className="imagesPreview-section">
                    {
                      registeredCasesInfo.companySupportDocs.map((docs, index) => 
                         <div className="imagesPreview" key={index}>
                           <a href= {docs}  target ="_blank" rel="noopener noreferrer">Preview Docs</a>
                         </div>
                      )
                    }
                  </div>
                </Col>
                <Col md={12}><hr />
                  <h3>Threat Information and Details</h3>
                </Col>
                <Col md={12}>
                    <Label><strong>Did a physical assault happen recently or in the past? </strong></Label>
                    <p>{registeredCasesInfo.physicalAssault}</p>
                    <p>Description: {registeredCasesInfo.physicalAssaultDescription}</p>

                </Col>
                <Col md={12}>
                    <Label><strong>Is Law Enforcement involved? </strong></Label>
                    <p>{registeredCasesInfo.lawEnforcement}</p>
                    <p>Description: {registeredCasesInfo.lawEnforcementDescription}</p>

                </Col>
                <Col md={12}>
                    <Label><strong>Is there a protection order place or planned? </strong></Label>
                    <p>{registeredCasesInfo.protectionOrder}</p>
                    <p>Description: {registeredCasesInfo.protectionOrderDescription}</p>

                </Col>
                <Col md={12}>
                    <Label><strong>Histor of violence? </strong></Label>
                    <p>{registeredCasesInfo.historyViolence}</p>
                    <p>Description: {registeredCasesInfo.historyViolenceDescription}</p>

                </Col>
                <Col md={12}>
                    <Label><strong>Hitory of weapons use or access to weapons? </strong></Label>
                    <p>{registeredCasesInfo.historyWeapon}</p>
                    <p>Description: {registeredCasesInfo.historyWeaponDescription}</p>

                </Col>
                <Col md={12}>
                    <Label><strong>History of heavy alcohol use/adiction? </strong></Label>
                    <p>{registeredCasesInfo.historyAlcohol}</p>
                    <p>Description: {registeredCasesInfo.historyAlcoholDescription}</p>

                </Col>
                <Col md={12}>
                    <Label><strong>Under the influnece at the time of the incident(s)? </strong></Label>
                    <p>{registeredCasesInfo.influnece}</p>
                    <p>Description: {registeredCasesInfo.influenceDescription}</p>

                </Col>

                <Col md={12}>
                    <Label><strong>Gang or extremist affiliations? </strong></Label>
                    <p>{registeredCasesInfo.gang}</p>
                    <p>Description: {registeredCasesInfo.gangDescription}</p>

                </Col>
                <Col md={12}>
                    <Label><strong>Known specialized training (law enforcement or military)? </strong></Label>
                    <p>{registeredCasesInfo.specializedTraining}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Known financial or personal problems (BK, Divorce)? </strong></Label>
                    <p>{registeredCasesInfo.financialProblem}</p>
                    

                </Col>
                <Col md={12}>
                    <Label><strong>Known employement issue? </strong></Label>
                    <p>{registeredCasesInfo.employmentIssue}</p>
                    

                </Col>
                <Col md={12}>
                    <Label><strong>Past suicidal ideation? </strong></Label>
                    <p>{registeredCasesInfo.pastSucidial}</p>
                    

                </Col>
                <Col md={12}><hr />
                  <h3>Grivances</h3>
                </Col>
                <Col md={12}>
                    <Label><strong>Financial or Material </strong></Label>
                    <p>{registeredCasesInfo.financial ? "Yes" : "No"}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Personal Prejudice (Racist/Extremist) </strong></Label>
                    <p>{registeredCasesInfo.personal ? "Yes" : "No"}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Work Performance or Sabotage </strong></Label>
                    <p>{registeredCasesInfo.workPerformance ? "Yes" : "No"}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Intimate Relationship Fixation </strong></Label>
                    <p>{registeredCasesInfo.intimateRelationship ? "Yes" : "No"}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Non-Intimate Relationship Fixation </strong></Label>
                    <p>{registeredCasesInfo.nonIntimateRelationship ? "Yes" : "No"}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Stalking Involved </strong></Label>
                    <p>{registeredCasesInfo.stalking ? "Yes" : "No"}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Grivances Description </strong></Label>
                    <p>{registeredCasesInfo.grievanceDescription}</p>
                </Col>
                <Col md={12}><hr />
                  <h3>Victim Assessment</h3>
                </Col>
                <Col md={12}>
                    <Label><strong>Pripr incidents of abuse? </strong></Label>
                    <p>{registeredCasesInfo.priprIncident}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Stable Living situation? </strong></Label>
                    <p>{registeredCasesInfo.stableLiving}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Employment/Education concerns? </strong></Label>
                    <p>{registeredCasesInfo.employmentConcern}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Current or planned security measures in place? </strong></Label>
                    <p>{registeredCasesInfo.currentSecurity}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Security training or enhanced skills? </strong></Label>
                    <p>{registeredCasesInfo.securityTraining}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Assisstance program offered? </strong></Label>
                    <p>{registeredCasesInfo.assignmentProgram}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Support system available(family/friends)? </strong></Label>
                    <p>{registeredCasesInfo.supportSystem}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Mental Health Concerns? </strong></Label>
                    <p>{registeredCasesInfo.mentalHealth}</p>
                </Col>
                <Col md={12}>
                    <Label><strong>Description of incidents? </strong></Label>
                    <p>{registeredCasesInfo.victimDescription}</p>
                </Col>
              </Row>
            </div>
          </CardBody>
        </Card>
        
      </div>

    )
  }
}

export default RegisteredCasesDetails;
